import React, {useEffect} from "react";

const BackgroundDots = () => {
    useEffect(() => {
        let amount = 30;
        let body = document.querySelector('body');
        let i = 0;
        while (i < amount) {
            let rands = Math.floor(Math.random() * 2) + 1;
            let node = document.createElement("i");
            let posX = Math.floor(Math.random() * document.documentElement.clientWidth);
            let posY = Math.floor(Math.random() * window.document.body.offsetHeight);
            let rotation = Math.random() * 180;
            let delay = Math.random() * 20;
            let scale = Math.random() * 0.2;
            node.style.left = posX + 'px';
            node.style.top = posY + 'px';
            node.style.transform = 'rotate(' + rotation + 'deg) scale(' + scale + ')';
            node.style.animationDelay = delay + 's';
            if (rands === 1) {
                node.style.backgroundColor = "#FF1C5E";
                node.style.boxShadow = "0px 0px 5px 3px #FF1C5E";
            } else {
                node.style.backgroundColor = "white";
                node.style.boxShadow = "0px 0px 5px 3px #fff";
            }
            body.appendChild(node);
            i++;

        }
    }, []);

    return null
}

export default BackgroundDots;