import React, { useEffect, useRef } from 'react';

const ModalInfo = ({ modalStyle, message1, message2, show, onClose, backdropStyle }) => {
    const modalRef = useRef(null);
    useEffect(
        () => {
            if (show) {
                modalRef.current.classList.add('visible');
            }
            else {
                modalRef.current.classList.remove('visible');
            }
        },
        [
            show
        ]
    );
    return (
        <React.Fragment>
            <div ref={modalRef} style={backdropStyle} className={`modal__wrap`}>
                <div style={modalStyle} className={'modal'}>
                    <div
                        onClick={onClose}
                        style={{ width: 30, height: 30, position: 'absolute', top: 0, right: 0, margin: '1rem', opacity: '.5', cursor: 'pointer' }}
                        className={'close__btn'}
                    >
                        <svg viewport="0 0 12 12" version="1.1"
                             color={'white'}
                             width='22' height='22'
                             xmlns="http://www.w3.org/2000/svg">
                            <line x1="1" y1="15"
                                  x2="15" y2="1"
                                  stroke="white"
                                  strokeWidth="2"/>
                            <line x1="1" y1="1"
                                  x2="15" y2="15"
                                  stroke="white"
                                  strokeWidth="2"/>
                        </svg>
                    </div>
                    <h3>{message1}</h3>
                    <h3 style={{marginTop: '15px'}}> {message2}</h3>
                </div>
            </div>
        </React.Fragment>
    );
};

export default ModalInfo;