import React, {useEffect, useState} from "react";
import {HashLink as Link} from 'react-router-hash-link';
import {useLocation} from 'react-router-dom';
import {useTranslation} from "react-i18next";
import ModalLogin from "./ModalLogin";
import ModalStore from "../store/ModalStore";
import ModalInfo from "./Modal";
import {observer} from "mobx-react-lite";


const Header =  observer(() => {
    const {t} = useTranslation();
    const {i18n} = useTranslation();
    let location = useLocation();
    let headerStyleVar = 0;
    const [isActive, setActive] = useState(false);
    const [isLangActive, setLangActive] = useState(1);
    const [isMenuActive, setMenuActive] = useState(false);
    let temp = location.pathname;
    if (temp.indexOf('roadmap') === -1)
        headerStyleVar = 0;
    else
        headerStyleVar = 1;

    return (
        <div>
            {headerStyleVar === 0 &&
                <div className="main_bg"/>
            }
            {headerStyleVar === 0 &&
                <div className="main_bg_blur"/>
            }
            <div className="header_blur"/>

            <div className="h_w">
                <header className={`container ${isMenuActive ? "menu_active" : ""}`}>
                    <div className="header_logo">
                        <Link to="/#bdy">
                            <img src="img/logo.svg" alt=""/>
                        </Link>
                    </div>
                    <div className="header_menu pc">
                        <div className="header_menu_item">
                            <Link to="/#partners">
                                {t("header_menu_item1")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/#participation">
                                {t("header_menu_item2")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/#what_talk">
                                {t("header_menu_item3")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/roadmap">
                                Roadmap
                            </Link>
                        </div>
                    </div>
                    <div className="header_lang pc">
                        <div className="header_lang_active" onClick={() => setActive(!isActive)}>
                            <span className="firstWord">{i18n.language}</span>
                            <img src="img/arrow_down.svg" alt="" className={`${isActive ? "img_rotate" : ""}`}/>
                        </div>
                        <div className={`header_lang_select ${isActive ? "active" : ""}`}>
                            {i18n.language === 'ru' &&
                                <span className="firstWord" onClick={async () => {
                                    await i18n.changeLanguage('en');
                                    setLangActive(1);
                                    setActive(!isActive);
                                }}>EN</span>
                            }
                            {i18n.language === 'en' &&
                                <span className="firstWord" onClick={async () => {
                                    await i18n.changeLanguage('ru');
                                    setLangActive(2);
                                    setActive(!isActive);
                                }}>RU</span>
                            }

                        </div>
                    </div>
                    <div className="btn_connect_wallet header_btn_connect_wallet pc"
                         onClick={() => ModalStore.setOpenLogin(true)}>
                        <img src="img/wallet.svg" alt=""/>
                        <span>{t("btn_connect_wallet")}</span>
                    </div>
                    <div className="menu mob">
                        <div className="menu_show" onClick={() => setMenuActive(!isMenuActive)}>
                            <img src="img/menu.svg" alt=""
                                 className={`menu_show_hidden ${!isMenuActive ? "active" : ""}`}/>
                            <img src="img/close.svg" alt=""
                                 className={`menu_show_active ${isMenuActive ? "active" : ""}`}/>
                        </div>
                    </div>
                </header>
                <div className={`menu_hidden mob ${isMenuActive ? "active" : ""}`}>
                    <div className="header_menu" onClick={() => setMenuActive(!isMenuActive)}>
                        <div className="header_menu_item">
                            <Link to="/#partners">
                                {t("header_menu_item1")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/#participation">
                                {t("header_menu_item2")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/#what_talk">
                                {t("header_menu_item3")}
                            </Link>
                        </div>
                        <div className="header_menu_item">
                            <Link to="/roadmap">
                                Roadmap
                            </Link>
                        </div>
                    </div>
                    <div className="btn_connect_wallet header_btn_connect_wallet"
                         onClick={() => ModalStore.setOpenLogin(true)}>
                        <img src="img/wallet.svg" alt=""/>
                        <span>{t("btn_connect_wallet")}</span>
                    </div>
                    <div className="header_lang">
                        <div className="header_lang_active" onClick={() => setActive(!isActive)}>
                            <span className="firstWord">{i18n.language}</span>
                            <img src="img/arrow_down.svg" alt="" className={`${isActive ? "img_rotate" : ""}`}/>
                        </div>
                        <div className={`header_lang_select ${isActive ? "active" : ""}`}>
                            {i18n.language === 'ru' &&
                                <span className="firstWord" onClick={async () => {
                                    await i18n.changeLanguage('en');
                                    setLangActive(1);
                                    setActive(!isActive);
                                    setMenuActive(!isMenuActive);
                                }}>EN</span>
                            }
                            {i18n.language === 'en' &&
                                <span className="firstWord" onClick={async () => {
                                    await i18n.changeLanguage('ru');
                                    setLangActive(2);
                                    setActive(!isActive);
                                    setMenuActive(!isMenuActive);
                                }}>RU</span>
                            }
                        </div>
                    </div>
                </div>
            </div>

            <ModalLogin show={ModalStore.openLogin} onClose={() => ModalStore.setOpenLogin(false)}/>
            <ModalInfo show={ModalStore.openInfo} onClose={() => ModalStore.setOpenInfo(false)}
                       message1={t('modalLoading1')} message2={t('modalLoading2')}/>
        </div>
    )
})

export default Header;